import "./style.css"
import { Panel } from '../panel'
//You write a function, etc., inside a module, packaged in a file (index.js)
export class Modal {

    get title() { return this.panel.title }
    set title(v) {
        this.panel.title = v
    }

    constructor(options) {
        const {
            containerId,
            title,
            content,
            expandContent = null,
            onClickButton = null,
            buttonTitle = '',
        } = options

        this._rootElm = document.querySelector(`#${containerId}`)
        this._rootElm.classList.add('modal')

        // add all the staffs
        this._rootElm.insertAdjacentHTML('beforeend', `
            <div class="background"></div>
            <div class="content">
                <div id="${containerId}_panelSlot"> </div>
            </div>
        `)

        this.panel = new Panel({
            containerId: `${containerId}_panelSlot`,
            title,
            content,
            expandContent,
            onClickButton,
            buttonTitle,
            center: true,
        })

        //     <div class="title">
        //     <strong>${title}</strong>
        // </div>
        // <div class="content center">
        //     <p>${content}</p>
        //     ${expandContent ? expandContent : ''}
        //     ${onClickButton
        // ? `<button class="button secondary">${buttonTitle}</button>`
        // : ''}
        // </div>
    } // lose constructor options


    appendContent(content) {
        this.panel.appendContent(content)
    }

    show() {
        this._rootElm.classList.add('active')
    }

    hide() {
        this._rootElm.classList.remove('active')
    }

    hideButton() {
        this.panel.hideButton()
    }

    showButton() {
        this.panel.showButton()
    }
} // close class Modal