import "./style.css"

export class Panel {

    get title() {
        return this._rootElm.querySelector('.title strong').textContent
    }
    set title(v) {
        this._rootElm.querySelector('.title strong')
            .textContent = v
    }

    constructor(options) {
        const {
            containerId,
            title,
            content,
            expandContent = null,
            onClickButton = null,
            buttonTitle = '',
            center = false,
            sticky = false,
            scroll = false,
        } = options

        this._rootElm = document.querySelector(`#${containerId}`)
        this._rootElm.classList.add('panel')
        if (sticky) {
            this._rootElm.classList.add('sticky')
        }
        this._expandContent = expandContent !== null
        this.hasBeenExpanded = false

        // add all the staffs
        this._rootElm.insertAdjacentHTML('beforeend', `
            <div class="title"> <strong>${title}</strong> </div>
            <div class="content ${center ? 'center' : ''}">
            ${Array.isArray(content)
                ? content.map(text => `<li>${text}</li>`).join('')
                : content}
            ${expandContent
                ? `<div id="expandContent" class="section hidden">
                    ${expandContent}
                </div>`
                : ''}
            ${onClickButton
                ? `<button class="button secondary">${buttonTitle}</button>`
                : ''}
            </div>
        `)

        // just hack
        if (scroll) {
            this._rootElm.classList.add('scroll')
            this._rootElm.style.bottom = `-500px`
            let last_known_scroll_position = 0;
            let ticking = false;

            document.addEventListener('scroll', (e) => {
                last_known_scroll_position = window.scrollY;

                if (!ticking) {
                    window.requestAnimationFrame(() => {
                        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop

                        console.log(this._rootElm.style.marginLeft)
                        const bottom = Math.min(scrollTop * 1.5 - 500, 100)
                        this._rootElm.style.bottom = `${bottom}px`

                        ticking = false;
                    });

                    ticking = true;
                }
            })
        }

        this._onClickButton = onClickButton
        if (onClickButton) {
            this._rootElm.querySelector('.content button.button')
                .addEventListener('click', (e) => onClickButton(e, this))
        }
    }

    appendContent(content) {
        if (this._expandContent) {
            this._rootElm.querySelector('#expandContent').insertAdjacentHTML('beforebegin', content)
        } else if (this._onClickButton) {
            this._rootElm.querySelector('button.button').insertAdjacentHTML('beforebegin', content)
        } else {
            this._rootElm.querySelector('.content').insertAdjacentHTML('beforeend', content)
        }
    }

    showExpandContent() {
        if (this._expandContent) {
            this._rootElm.querySelector('.content #expandContent').classList.remove('hidden')
            this.hasBeenExpanded = true
        }
    }

    hideExpandContent() {
        if (this._expandContent) {
            this._rootElm.querySelector('.content #expandContent').classList.add('hidden')
            this.hasBeenExpanded = false
        }
    }

    hideButton() {
        if (this._onClickButton) {
            this._rootElm.querySelector('.content button.button')
                .style.display = 'none'
        }
    }

    showButton() {
        if (this._onClickButton) {
            this._rootElm.querySelector('.content button.button')
                .style.display = 'initial'
        }
    }
}